top-bar {
  height: 95px;
  position: relative;
  z-index: 1000;
  background-color: #ffffff; }
  top-bar > .row {
    width: 100%; }
  top-bar .row {
    height: 100%; }
  top-bar .col-xs {
    flex-grow: 0;
    height: 100%;
    display: flex;
    align-items: center;
    position: relative; }
  top-bar .col-xs:nth-of-type(1) {
    flex-basis: 300px;
    justify-content: center; }
  top-bar .col-xs:nth-of-type(2) {
    flex-grow: 1; }
  top-bar .col-xs:nth-of-type(3) {
    flex-basis: 300px;
    border-left: 1px solid #eeeeee;
    justify-content: flex-end; }
  top-bar .logo-con {
    font-size: 30px;
    color: #ffffff;
    text-shadow: 1px 1px 1px #e87a90; }
  top-bar .search-form .search-group {
    border-bottom: 1px solid #9e9e9e; }
    top-bar .search-form .search-group input {
      border: 0;
      width: 425px;
      height: 30px;
      background-color: transparent; }
      top-bar .search-form .search-group input:focus {
        outline: none; }
    top-bar .search-form .search-group button {
      border: 0;
      background-color: transparent;
      color: #ffffff;
      height: 30px; }
      top-bar .search-form .search-group button iron-icon {
        color: #9e9e9e; }
  top-bar .upload-video-button {
    height: 35px;
    line-height: 35px;
    padding-top: 0;
    padding-bottom: 0;
    position: absolute;
    left: auto;
    top: 0;
    right: 50px;
    bottom: 0;
    margin: auto; }
  top-bar .member {
    padding-right: 10px; }
    top-bar .member__action {
      margin: 0;
      padding-right: 10px; }
      top-bar .member__action paper-button {
        width: 26px;
        min-width: 0;
        padding: 0; }
        top-bar .member__action paper-button iron-icon {
          width: 20px;
          height: 20px; }
    top-bar .member__toggle-nav {
      width: 35px;
      padding: 0;
      margin-right: 20px;
      min-width: 0;
      color: #9e9e9e; }
  top-bar .nav-menu {
    width: 300px;
    position: absolute;
    left: 0;
    top: 95px;
    z-index: 1;
    border-top: 1px solid #eeeeee; }
    top-bar .nav-menu paper-card {
      width: 100%;
      border: 0;
      box-shadow: none;
      background-color: #f7f8fa;
      border-top-left-radius: 0;
      border-top-right-radius: 0; }
      top-bar .nav-menu paper-card paper-material {
        border-radius: 0 !important;
        box-shadow: none !important; }
      top-bar .nav-menu paper-card .header.paper-card .title-text.paper-card {
        padding-bottom: 0;
        color: #f44336;
        font-size: 16px;
        font-size: 1.6rem; }
      top-bar .nav-menu paper-card .card-content {
        padding-top: 10px;
        padding-bottom: 10px; }
    top-bar .nav-menu paper-menu {
      background-color: #f7f8fa; }
  top-bar .user-menu {
    width: 300px;
    position: absolute;
    right: 0;
    top: 95px;
    z-index: 1;
    border-top: 1px solid #eeeeee;
    background-color: #f7f8fa; }
    top-bar .user-menu paper-card {
      width: 100%;
      border: 0;
      box-shadow: none;
      background-color: #f7f8fa;
      border-top-left-radius: 0;
      border-top-right-radius: 0; }
      top-bar .user-menu paper-card paper-material {
