  align-items: center;
  background: rgba(233, 233, 233, 0.8);
  border-radius: 5px;
  position: absolute;
  bottom: 15%;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1; }
.lfx-player__progress {
  position: absolute;
  bottom: 15px;
  left: 0;
  width: 100%;
  z-index: 1; }
  .lfx-player__progress paper-progress {
